<template>
    <div class="test-field">
        <select v-model="test_num">
            <option disabled :value="0">Select the test</option>
            <option :value="3">Connect</option>
            <option :value="4">Disconnect</option>
            <option :value="5">Register</option>
            <option :value="6">User Update</option>
            <option :value="7">User Info</option>
            <option :value="8">Users List</option>
        </select>
        <button :disabled="this.test_num==0" :class="{active:this.test_num!=0}" v-on:click="testRequest()">Run Test</button>
        <div class="test-decor">
            <h2> Command type test: </h2>
            <p :class="{checked:CommandType!=''}">Command: {{ CommandType }}</p>
        </div>
        <div class="test-decor">
            <h2>User Id tets:</h2>
            <p :class="{checked:UserId!=0}">User Id: {{ UserId }}</p>
        </div>
        <div class="test-decor">
            <h2> User Name test:</h2>
            <p :class="{checked:UserName!=''}"> User name: {{ UserName }}</p>
        </div>
        <div class="test-decor">
            <h2>User Parsmeter test:</h2>
            <p :class="{checked:UserParameter.Name!=''}">Name: {{ UserParameter.Name }}</p>
            <p :class="{checked:UserParameter.Surname!=''}">Surname: {{ UserParameter.Surname }}</p>
            <p :class="{checked:UserParameter.Familyname!=''}">Familyname: {{ UserParameter.Familyname }}</p>
            <p :class="{checked:UserParameter.Birthdate!=''}">Birthdate: {{ UserParameter.Birthdate }}</p>
        </div>
        <div class="test-decor">
            <h2> Users List Test:</h2>
            <p :class="{checked:UsersLlist.Type!=''}"> Command: {{ UsersLlist.Type }}</p>
            <p> Number Of Items: {{ UsersLlist.NumOfItems }} </p>
            <ul class="users-list">
            <li v v-for="item in UsersLlist.Items" :key="item.id">
                <div class="item-in-list">
                <p>{{ item.name }}</p>
                <p :class="{online:item.online, offline:!item.online}">{{ onlineStatus(item.online) }}</p>
                <p>{{ item.nrm }}</p>
                </div>
            </li>
            </ul>
        </div>
        <div class="test-decor">
            <h2> Coding result: </h2>
            <p>Recieve message: {{ origin }}</p>
            <p>Coding result: {{ codingRes }}</p>
        </div>
    </div>
</template>

<script>

export default{
    props:{
        CommandType:{
            type: String,
            default: "",
        },
        UserId:{
            type: Number,
            default: 0,
        },
        UserName: {
            type: String,
            default: "",
        },
        UserParameter:{
            type: Object,
            default: () =>({Name: "", Surname: "", Familyname: "", Birthdate: ""}),
        },
        UsersLlist:{
            type: Object,
            default: () => ({Type: "", NumOfItems: 0, Items: []}),
        },
        codingRes:{
            type: Array,
            default: () => [],
        },
        origin:{
            type: Array,
            default: () => [],
        }
    },
    data(){
        return {
            test_num: 0,            
        }
    },
    methods:{
        testRequest(){
            return this.$emit('runTest', this.test_num)
        },
        onlineStatus(online){
        if (online){
            return 'online';
        }else{
            return 'offline';
        }
        }
    }
}

</script>


<style>
</style>

