<template>
<div class="test-field">
    <select v-model="test_num">
            <option disabled :value="0">Select the test</option>
            <option :value="1">Send Result Test</option>
            <option :value="2">Recieve Result Test</option>
            <option :value="3">Read Result Test</option>
        </select>
    <button :disabled="this.test_num==0" :class="{active:this.test_num!=0}" v-on:click="testRequest()">Run Test</button>
    <div class="test-decor">
        <h2>Data Type test:</h2>
        <p :class="{checked:AckType!=''}">Type: {{ AckType }}</p>
    </div>
    <div class="test-decor">
        <h2>Index Number test:</h2>
        <p :class="{checked:IndexNumber!=0}">Index Number: {{ IndexNumber }}</p>
    </div>
    <div class="test-decor">
        <h2>User Id test:</h2>
        <p :class="{checked:UserId!=0}">User Id: {{ UserId }}</p>
    </div>
    <div class="test-decor">
        <h2>Room Id test:</h2>
        <p :class="{checked:isNaN(RoomId)==false}">Room Id: {{ RoomId }}</p>
    </div>
    <div class="test-decor">
        <h2>Coding result:</h2>
        <p>Recieve message: {{ origin }}</p>
        <p>Coding result: {{ codingRes }}</p>
    </div>
</div>
</template>

<script>

export default{
    props:{
        AckType:{
            type: String,
            default: "",
        },
        IndexNumber:{
            type: Number,
            default: 0,
        },
        UserId:{
            type: Number,
            default: 0,
        },
        RoomId:{
            type: Number,
            default: NaN,
        },
        codingRes:{
            type: Array,
            default: () => [],
        },
        origin:{
            type: Array,
            default: () => [],
        }
    },
    data(){
        return{
            test_num:0,
        }
    },
    methods:{
        testRequest(){
            return this.$emit('runTest', this.test_num)
        },
    }
}
</script>

