<template>
    <h1>Testing Protocol</h1>
    <div class="container-test">
        <div class="test-button-block">
            <button :disabled="isControlButtonEnable" :class="{active:isControlButtonEnable}" v-on:click="pressControlButton()">Control Test</button>
            <button :disabled="isDataButtonEnable" :class="{active:isDataButtonEnable}" v-on:click="pressDataButton()">Data Test</button>
            <button :disabled="isAcknowledgeEnable" :class="{active:isAcknowledgeEnable}" v-on:click="pressAcknowledgeButton()">Acknowelege Test</button>
        </div>
        <ControlTest v-if="isControlButtonEnable"
        v-bind:CommandType="CommandType"
        v-bind:UserId="UserId"
        v-bind:UserName="UserName"
        v-bind:UserParameter="UserParameter"
        v-bind:UsersLlist="UsersLlist"
        v-bind:codingRes="codingRes"
        v-bind:origin="origin"
        @runTest="testRequest($event)"
        />
        <DataTest v-if="isDataButtonEnable"
        v-bind:DataType="DataType"
        v-bind:DataFormat="DataFormat"
        v-bind:IndexNumber="IndexNumber"
        v-bind:UserId="UserId"
        v-bind:RoomId="RoomId"
        v-bind:Message="Message"
        v-bind:codingRes="codingRes"
        v-bind:origin="origin"
        @runTest="testRequest($event)"
        />
        <AcknowledgeTest v-if="isAcknowledgeEnable"
        v-bind:AckType="AcknowledgeType"
        v-bind:IndexNumber="IndexNumber"
        v-bind:UserId="UserId"
        v-bind:RoomId="RoomId"
        v-bind:codingRes="codingRes"
        v-bind:origin="origin"
        @runTest="testRequest($event)"
        />
    </div>
</template>

<script>

import ControlTest from '@/components/ControlTest.vue'
import DataTest from '@/components/DataTest.vue';
import AcknowledgeTest from '@/components/AcknowledgeTest.vue';
import { fetchPost } from '@/api/netork';

import {protocol} from '@/api/protocol.js';

export default{
    components:{
        ControlTest,
        DataTest,
        AcknowledgeTest,
    },
    data(){
        return {
            isControlButtonEnable: true,  
            isDataButtonEnable: false,
            isAcknowledgeEnable: false,
            testMode: 0,         
            CommandType: "",
            DataType: "",
            DataFormat: "",
            AcknowledgeType: "",
            IndexNumber: 0,
            UserId: 0,
            RoomId: 0,
            UserName: "",
            UserParameter: {
                Name: "",
                Surname:"",
                Familyname: "",
                Birthdate: "",
            },
            UsersLlist:{
                Type: "",
                NumOfItems:0,
                Items:[],
            },
            Message:{
                Fragmentation: false,
                FragmentType: "",
                Counter: 0,
                Data: Uint8Array,
            },          
            codingRes:[],
            origin:[],
        }
    },
    methods:{
        pressControlButton(){
            this.isDataButtonEnable= false;
            this.isControlButtonEnable= true;
            this.isAcknowledgeEnable = false;
            this.clearPreviosDataResult();
            this.testMode = 0;
        },
        pressDataButton(){
            this.isDataButtonEnable= true;
            this.isControlButtonEnable= false;
            this.isAcknowledgeEnable = false;
            this.clearPreviosControlResult();
            this.testMode = 1;
        },
        pressAcknowledgeButton(){
            this.isDataButtonEnable= false;
            this.isControlButtonEnable= false;
            this.isAcknowledgeEnable = true;
            this.clearPreviosAckResult();
            this.testMode = 2;
        },
        testRequest(value){
            let request = new Uint8Array(2);
            request[0] = this.testMode
            request[1] = value;
            fetchPost('/test', request)
            .then(resp => {
                if (resp.status != 200){
                    console.log('error:' + resp.status);
                    return;
                }
                resp.arrayBuffer().then(data => {
                    let message = new Uint8Array(data);
                    switch (message[0]){
                        case 0:
                            this.controlCommandDecode(message.subarray(1));
                            break;
                        case 1:
                            this.dataMessageDecode(message.subarray(1));
                            break; 
                        case 2:
                            this.ackMessageDecode(message.subarray(1));
                            break;       
                    }
                    this.origin = [...message];
                   
                })
            })
            .catch(err => console.error(err));
            
        },
        controlCommandDecode(data){
            let control = protocol.ControlCommand;
            this.clearPreviosControlResult();
            control.clear();
            control.decode(data);
            this.processingControlCommand(control);
            this.codingRes = [...control.code()];
        },
        clearPreviosControlResult(){
            this.CommandType = "";
            this.UserId = 0;
            this.UserName = "";
            this.UserParameter.Name = "";
            this.UserParameter.Surname = "";
            this.UserParameter.Familyname = "";
            this.UserParameter.Birthdate = "";
            this.UsersLlist.Type = "";
            this.UsersLlist.NumOfItems = 0;
            this.UsersLlist.Items.splice(0,this.UsersLlist.Items.length);
            this.origin.length = 0;
            this.codingRes.length = 0; 
        },
        processingControlCommand(control){
            this.CommandType = control.ControlMessageType.toString()
            switch (control.ControlMessageType.toString()){
            case "connect":
            case "disconnect":
                this.UserId = control.UserId;
                break;
            case "registration":
            case "user update":
                this.UserId = control.UserId;  
                this.UserName = control.UserName;
                break;
            case "user info":
                this.UserParameter.Name = control.UserParameter.Name;
                this.UserParameter.Surname = control.UserParameter.Surname;
                this.UserParameter.Familyname = control.UserParameter.Familyname;
                this.UserParameter.Birthdate = control.UserParameter.Birthdate;
                break;
            case "users list":
                this.UsersLlist.Type = control.UsersList.ListCommand;
                this.UsersLlist.NumOfItems = control.UsersList.NumberOfItems;
                for (let element of control.UsersList.ListElements){
                    this.UsersLlist.Items.push({id: element[0],name:element[1].name, online: element[1].online, nrm: element[1].nrm});
                }
                break;
            }
        },
        dataMessageDecode(raw){
            let data = protocol.Data;
            this.clearPreviosDataResult();
            data.clear();
            data.decode(raw);
            this.processingDataMessage(data);
            this.codingRes = [...data.code()];
        },
        clearPreviosDataResult(){
            this.DataType = "";
            this.DataFormat = "";
            this.IndexNumber = 0;
            this.UserId = 0;
            this.RoomId = NaN;
            this.Message.Fragmentation = false;
            this.Message.FragmentType = "";
            this.Message.Counter = 0;
            this.Message.Text = "";
            this.origin.length = 0;
            this.codingRes.length = 0;
        },
        processingDataMessage(data){
            this.DataType = data.DataMessageType.toString();
            this.DataFormat = data.DataMessageFormat.toString();
            this.IndexNumber = data.IndexNumber;
            this.UserId = data.UserId;
            this.RoomId = data.RoomId;
            this.Message.Fragmentation = data.UserData.Fragmentation.On;
            this.Message.FragmentType = protocol.FragmentTypeToString(data.UserData.Fragmentation.FragmentType),
            this.Message.Counter = data.UserData.Fragmentation.Counter;
            this.Message.Data = data.UserData.Data;
        },
        ackMessageDecode(raw){
            let ack = protocol.Acknowledge;
            this.clearPreviosAckResult();
            ack.clear();
            ack.decode(raw);
            this.processingAckMessage(ack);
            this.codingRes = [...ack.code()];
        },
        clearPreviosAckResult(){
            this.AcknowledgeType = "";
            this.IndexNumber = 0;
            this.UserId = 0;
            this.RoomId = NaN;
            this.origin.length = 0;
            this.codingRes.length = 0;
        },
        processingAckMessage(ack){
            this.AcknowledgeType = ack.AckMessageType.toString();
            this.IndexNumber = ack.IndexNumber;
            this.UserId = ack.UserId;
            this.RoomId = ack.RoomId;
        }
    },
}

</script>

<style>

.test-button-block{
    width: 100%;
    min-height: 50px;
    display: flex;
    justify-content: center;
    flex-direction: row;
    margin: 0;
    margin-top: 10px;
    padding: 5px 0;
    box-sizing: border-box;    
}

.test-button-block button{
    margin: 0;
    width: 150px;
    align-items: center;
    border: solid 1px black;
    font-size: 15px;
    font-weight: 700;
    cursor: pointer;
}
.test-button-block .active{
    background-color: rgb(63, 151, 123);
    color: #fff;
    cursor: auto;
}

.test-field{
    width: 100%;
    height: 100%;
    align-items: center;
    padding: 10px 0;
    margin: 0;
    display: flex;
    flex-direction: column;
}

.test-field select{
    margin-bottom: 20px;
    padding: 0 10px;
    width: 200px;
    min-height: 40px;
    margin-left: auto;
    margin-right: auto;
    font-size: 20px;
    font-weight: 700;
    border-radius: 5px;
    box-shadow: 0px 3px 2px rgba(0, 0, 0, .1);
    cursor: pointer;
}

.test-field button{
    margin-bottom: 10px;
    width: 150px;
    min-height: 40px;
    box-sizing: border-box;
    border-radius: 8px;
    font-size: 14pt;
    font-weight: 600;
    text-decoration: none;

    background-color: rgb(203, 222, 212);
    color: rgb(88, 104, 91);
    border: none;
}

.test-field .active{
    border: solid 1.5px white;
    background-color: rgb(54, 214, 128);
    color: #fff;
    cursor: pointer;
}

.test-field .active:hover{
    background-color: rgb(28, 110, 66);
}

.test-decor{
  margin: 2px auto;
  padding: 5px;
  min-width: 50%;
  max-width: 90%;
  border: solid 2px;
  border-radius: 20px;
}

.test-decor h2{
  margin: 0;
  font-size: 20px;
  font-weight: bold;
  padding: 5px 0;
  color: rgb(22, 47, 112);
}

.test-decor p{
    font-size: 18px;
    font-weight: 500;
    margin: 0;
    padding: 5px 0;
}

.checked {
    font-weight: bold !important;
    color: rgb(16, 101, 23);
}
.checked::after{
    padding-left: 15px;
    font-size: 18px;
    font-weight: bold;
    content: '\2713';
}

.users-list li{
  list-style: none;
}

.item-in-list{
  display: inline-flex;
  border-bottom: solid 2px;
  margin: auto;
}

.item-in-list p{
  margin: 10px 0;
  padding: 2px 20px;
  font-weight: bold;
}

.offline{
  border: none;
  border-radius: 5px;
  background-color: red;
  color: white;
}

.online{
  border: none;
  border-radius: 5px;
  background-color: green;
  color: white;
}

</style>
