<template>
<div class="test-field">
    <select v-model="test_num">
            <option disabled :value="0">Select the test</option>
            <option :value="1">Small text</option>
            <option :value="2">Big text</option>
            <option :value="3">First fragment</option>
            <option :value="4">Last ftagment</option>
            <option :value="5">Image</option>
        </select>
    <button :disabled="this.test_num==0" :class="{active:this.test_num!=0}" v-on:click="testRequest()">Run Test</button>
    <div class="test-decor">
        <h2>Data Type test:</h2>
        <p :class="{checked:DataType!=''}">Type: {{ DataType }}</p>
    </div>
    <div class="test-decor">
        <h2>Data Format test:</h2>
        <p :class="{checked:DataFormat!=''}">Format: {{ DataFormat }}</p>
    </div>
    <div class="test-decor">
        <h2>Index Number test:</h2>
        <p :class="{checked:IndexNumber!=0}">Index Number: {{ IndexNumber }}</p>
    </div>
    <div class="test-decor">
        <h2>User Id test:</h2>
        <p :class="{checked:UserId!=0}">User Id: {{ UserId }}</p>
    </div>
    <div class="test-decor">
        <h2>Room Id test:</h2>
        <p :class="{checked:isNaN(RoomId)==false}">Room Id: {{ RoomId }}</p>
    </div>
    <div class="test-decor">
        <h2>Message Data test:</h2>
        <p :class="{checked:Message.Fragmentation==true}">Fragmentation: {{ Message.Fragmentation }}</p>
        <p :class="{checked:Message.Fragmentation==true}">Fragment Type: {{ Message.FragmentType }}</p>
        <p :class="{checked:Message.Fragmentation==true}">Counter: {{ Message.Counter }}</p>
        <p :class="{checked:dataTextProcessing(Message.Data)!=''}" v-if="DataFormat=='text'">Text: {{ dataTextProcessing(Message.Data) }}</p>
        <img :src=dataImageProcessing(Message.Data) alt="" v-if="DataFormat=='image'">
    </div>
    <div class="test-decor">
        <h2>Coding result:</h2>
        <p>Recieve message: {{ origin }}</p>
        <p>Coding result: {{ codingRes }}</p>
    </div>
</div>
</template>

<script>

export default{
    props:{
        DataType:{
            type: String,
            default: "",
        },
        DataFormat:{
            type: String,
            default: "",
        },
        IndexNumber:{
            type: Number,
            default: 0,
        },
        UserId:{
            type: Number,
            default: 0,
        },
        RoomId:{
            type: Number,
            default: NaN,
        },
        Message:{
            type: Object,
            default:() => ({Fragmentation:false, FragmentType:"", Counter:0, Data: Uint8Array}),
        },
        codingRes:{
            type: Array,
            default: () => [],
        },
        origin:{
            type: Array,
            default: () => [],
        }
    },
    data(){
        return{
            test_num:0,
        }
    },
    methods:{
        testRequest(){
            return this.$emit('runTest', this.test_num)
        },
        dataTextProcessing(data){
            let text = new TextDecoder();
            return text.decode(data) 
        },
        dataImageProcessing(data) {
            let str = btoa(String.fromCharCode(...data));
            return `data:image/jpeg;base64,${str}`;
        }
    }
}
</script>

